define('harvestman/product/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    model: function model() {
      var products = this.store.query('product', {});
      return Ember['default'].RSVP.hash({
        products: products,
        isIndex: true
      });
    }
  });

});