define('harvestman/page/route', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    model: function model() {
      var pages = this.store.query('page', {});
      return Ember['default'].RSVP.hash({
        pages: pages
      });
    }
  });

});